@import url("https://fonts.googleapis.com/css2?family=Red+Rose:wght@300;400;500;600;700&display=swap");
* {
  margin: 0;
  padding: 0;
  font-family: "Red Rose", cursive;
}

body.active {
  overflow-y: hidden;
}

.Header {
  margin: 2.99% 6.94% 0 6.94%;
}
.Header .Navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.Header .Navigation .logo {
  width: 16.18vw;
}
.Header .Navigation .exit {
  display: none;
}
.Header .Navigation ul {
  list-style: none;
  display: flex;
  gap: 48px;
  align-items: flex-start;
  padding: 0;
}
.Header .Navigation ul a {
  text-decoration: none;
  color: #434343;
  font-size: 1.39vw;
  font-weight: 400;
  line-height: 24.98px;
}
.Header .Navigation ul .btnt {
  display: none;
}
.Header .Navigation .btn {
  width: 11.81vw;
  height: 48px;
  border-radius: 10px;
  background: #A02279;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.11vw;
  font-weight: 400;
  line-height: 19.9px;
  color: #fff;
  cursor: pointer;
}
.Header .Navigation .btn:hover {
  background: #e63cb0;
}
.Header .Navigation .media_nav {
  display: none;
}
.Header .modal, .Header .fixed_modal {
  position: fixed;
  width: 100%;
  height: 100vh;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.Header .modal .fixed_modal, .Header .fixed_modal .fixed_modal {
  background: rgba(64, 64, 64, 0.3);
}
.Header .modal .content, .Header .fixed_modal .content {
  background: #fff;
  position: absolute;
  top: 247px;
  left: 27%;
  width: 600px;
  border-radius: 16px;
}
.Header .modal .content .connect, .Header .fixed_modal .content .connect {
  display: flex;
  justify-content: space-between;
  padding: 24px 32px;
  border-bottom: 1px solid #CFD8DC;
}
.Header .modal .content .connect h3, .Header .fixed_modal .content .connect h3 {
  font-weight: 700;
  font-size: 24px;
  line-height: 30px;
}
.Header .modal .content .para, .Header .fixed_modal .content .para {
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  margin: 32px 0 16px 32px;
}
.Header .modal .content .wallet, .Header .fixed_modal .content .wallet {
  display: flex;
  justify-content: space-between;
  border: 1px solid #CFD8DC;
  padding: 12px 20px;
  border-radius: 12px;
  margin: 16px 32px;
}
.Header .modal .content .wallet .wallet_img, .Header .fixed_modal .content .wallet .wallet_img {
  display: flex;
  align-items: center;
}
.Header .modal .content .wallet .wallet_img p, .Header .fixed_modal .content .wallet .wallet_img p {
  font-weight: 600;
  font-size: 18px;
  line-height: 23px;
  padding-left: 16px;
}
.Header .modal .content .wallet:hover, .Header .fixed_modal .content .wallet:hover {
  background: #F8F9FA;
}

.HeaderSection {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  padding: 0;
  gap: 6.88%;
  margin: 8.67% 8.26% 4.65% 6.94%;
}
.HeaderSection .sales {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 0;
  gap: 48px;
  width: 44.86vw;
}
.HeaderSection .sales h1 {
  font-style: normal;
  font-weight: 400;
  font-size: 3.89vw;
  letter-spacing: -0.02em;
  color: #434343;
}
.HeaderSection .sales h1 span {
  color: #A02279;
  font-weight: 500;
}
.HeaderSection .sales p {
  font-family: "Red Rose";
  font-style: normal;
  font-weight: 400;
  font-size: 1.67vw;
  line-height: 35px;
  color: #434343;
}
.HeaderSection .sales div {
  width: 618.71px;
  height: 54.08px;
}
.HeaderSection .sales div input {
  background: rgba(247, 247, 247, 0.1);
  border: 1px solid #A3A3A3;
  border-radius: 8px 0px 0px 8px;
  box-sizing: border-box;
  width: 27.08vw;
  height: 54px;
}
.HeaderSection .sales div ::-moz-placeholder {
  font-Weight: 400;
  font-Size: 14px;
  line-height: 18.2px;
  padding-left: 15px;
}
.HeaderSection .sales div :-ms-input-placeholder {
  font-Weight: 400;
  font-Size: 14px;
  line-height: 18.2px;
  padding-left: 15px;
}
.HeaderSection .sales div ::placeholder {
  font-Weight: 400;
  font-Size: 14px;
  line-height: 18.2px;
  padding-left: 15px;
}
.HeaderSection .sales div span {
  background: linear-gradient(90deg, #A02279 11.45%, #A02279 11.46%);
  border-radius: 0px 8px 8px 0px;
  width: 15.97vw;
  height: 54px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}
.HeaderSection .sales div span a {
  text-decoration: none;
  color: #fff;
  line-height: 19px;
}
.HeaderSection .sales_img img {
  width: 33.06vw;
}

.Main .main_header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 10px 100px;
  background: linear-gradient(90deg, #A02279 11.45%, #A02279 11.46%);
}
.Main h2 {
  margin: 58px 0 43px 0;
  text-align: center;
  font-size: 3.33vw;
  font-weight: 700;
  line-height: 59.95px;
}
.Main .main_img {
  margin: 0 6.94%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 1.67vw;
}
.Main .main_img .img_card {
  border: 1px solid #D7D7D7;
  border-radius: 15px;
  padding: 1.11vw;
}
.Main .main_img .img_card .frame {
  width: 18.06vw;
}
.Main .main_img .img_card .span_div {
  display: flex;
  justify-content: space-between;
  margin: 5px 0;
}
.Main .main_img .img_card .span_div span {
  font-size: 12px;
  font-weight: 400;
  color: #434343;
}
.Main .main_img .img_card .span_div .spant {
  font-weight: 700;
}
.Main .main_img .img_card .rating {
  display: flex;
  gap: 8px;
}
.Main section {
  display: flex;
  align-items: center;
  background: linear-gradient(90deg, #A02279 11.45%, #A02279 11.45%);
  margin-top: 53px;
  -moz-column-gap: 9.03vw;
       column-gap: 9.03vw;
}
.Main section div {
  padding-left: 6.94vw;
}
.Main section div h2 {
  font-weight: 700;
  font-size: 3.33vw;
  line-height: 60px;
  color: #fff;
  text-align: start;
}
.Main section div p {
  font-weight: 400;
  font-size: 1.25vw;
  line-height: 35px;
  color: #fff;
}
.Main section div span {
  width: 156px;
  height: 48px;
  border-radius: 8px;
  background: #fff;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  margin-top: 57.55px;
}
.Main section div span a {
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #A02279;
  text-decoration: none;
}
.Main section img {
  padding: 100px 6.88vw 99.15px 0;
  width: 48.89vw;
}

.PlaceToStay {
  margin: 115px 100px 92px 100px;
}
.PlaceToStay .place {
  margin-bottom: 64px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.PlaceToStay .place ul {
  list-style: none;
  display: flex;
  gap: 3.33vw;
  align-items: flex-start;
  padding: 0;
}
.PlaceToStay .place ul a {
  text-decoration: none;
  color: #434343;
  font-size: 1.39vw;
  font-weight: 400;
  line-height: 24.98px;
}
.PlaceToStay .place .bolt {
  width: 11.18vw;
  height: 48px;
  border-radius: 8px;
  border: 1px solid #B4B4B4;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.PlaceToStay .place .bolt a {
  text-decoration: none;
  color: #434343;
  font-size: 1.39vw;
  font-weight: 400;
  line-height: 24.98px;
}
.PlaceToStay .main_img {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 1.67vw;
}
.PlaceToStay .main_img .img_card {
  border: 1px solid #D7D7D7;
  border-radius: 15px;
  padding: 1.11vw;
}
.PlaceToStay .main_img .img_card .frame {
  width: 18.06vw;
}
.PlaceToStay .main_img .img_card .span_div {
  display: flex;
  justify-content: space-between;
  margin: 5px 0;
}
.PlaceToStay .main_img .img_card .span_div span {
  font-size: 12px;
  font-weight: 400;
  color: #434343;
}
.PlaceToStay .main_img .img_card .span_div .spant {
  font-weight: 700;
}
.PlaceToStay .main_img .img_card .rating {
  display: flex;
  gap: 8px;
}

.Footer {
  background: #1D1D1E;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  padding: 58px 7.85vw 37px 7.85vw;
  grid-gap: 10.42vw;
}
.Footer .footer_logo .foot_logo {
  padding-bottom: 99.56px;
}
.Footer .footer_logo div {
  padding-bottom: 68.24px;
}
.Footer .footer_logo div img {
  padding-right: 20px;
  transition: 0.3s;
}
.Footer .footer_logo div img:hover {
  filter: invert(15%) sepia(46%) saturate(5635%) hue-rotate(304deg) brightness(96%) contrast(85%);
}
.Footer .footer_logo p {
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  color: #fff;
}
.Footer .footer_drop .footer_icon {
  font-weight: 700;
  font-size: 18px;
  padding-bottom: 22px;
  color: #fff;
  cursor: pointer;
}
.Footer .footer_drop .portal p {
  font-weight: 400;
  font-size: 14px;
  line-height: 35px;
  transition: 0.4s;
}
.Footer .footer_drop .portal p a {
  color: #F1F3F9;
  text-decoration: none;
  transition: 0.4s;
}
.Footer .footer_drop .portal p a:hover {
  color: #A02279;
}

