    width: 37.5vw;
  }
  .Footer {
    grid-template-columns: repeat(2, 1fr);
    row-gap: 15vw;
  }
  .Footer .footer_logo .foot_logo {
    padding-bottom: 59.56px;
  }
  .Footer .footer_logo div {
    padding-bottom: 38.24px;
  }
}
